:root {
    /*Fonts*/
    --font-open: 'Open Sans', serif;
    --font-saira: 'Saira Extra Condensed', serif;
    /*Color*/
    --color-primary: #343a40;
    --color-segundar: #868e96;
    --color-brown: #bd5d38;
    /*Background*/
    --bg-defaut: #bd5d38;
    /*Margin*/
    --mrt-auto: auto;
    --mrb-auto: auto;
    /*Display flex*/
    --dp-flex: flex !important;
}
body {
    padding-top: 0;
    padding-left: 17rem;
    font-family: var(--font-open);
    font-size: 1rem;
    font-weight: 400;
    color: var(--color-segundar);
    line-height: 1.5;
}

::-webkit-scrollbar {
    width: 6px;
}
  
/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1; 
}
   
/* Handle */
::-webkit-scrollbar-thumb {
    background: #999; 
}
  
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555; 
}

@media screen and (max-width: 992px) {
    body {
        padding-left: 0rem;
    }
}

@media screen and (max-width: 320px) {
    section#about,
    section#experience,
    section#education,
    section#skills,
    section#interests,
    section#awards{
        padding: 1rem 1rem;
    }
}
#navFixed{position:fixed;top:0;left:0;display:flex;flex-direction:column;justify-content:center;align-items:center;width:17rem;height:100vh}nav{background:var(--bg-defaut) !important}nav .profile img{max-width:10rem;max-height:10rem;border:0.5rem solid rgba(255,255,255,0.2);border-radius:50% !important;margin-bottom:.5rem}menu.menu{padding:0px;margin:0px;display:flex;flex-direction:column;align-items:center;width:90%}menu.menu .item{display:block;padding-top:.5rem;padding-bottom:.5rem}menu.menu a{text-decoration:none;font-weight:600;color:rgba(255,255,255,0.5);text-transform:uppercase}menu.menu a:hover{color:rgba(255,255,255,0.75)}menu.menu a.active{color:#fff}@media screen and (max-width: 992px){#navFixed{display:none}}

